@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap');
@import "react-slideshow-image/dist/styles.css";

*{
    padding: 0;
    margin: 0;
    overflow-x: hidden;
}

.wrapper{
    width: 100vw;
    height: 100vh;
    background-color: rgb(34, 34, 34);
}

.buyTicket{
    padding-top: 30px;
    padding-bottom: 30px;
    color: #F6CB4B;
    text-align: center;
}


.buyTicketLink{
    font-family: 'Open Sans', sans-serif;
    text-decoration: none;
    font-size: 24px;
    color: #F6CB4B;

}

.carouselPhoto{
    width: 270px;
    height: 270px;
    position: absolute;
    margin: 0 auto;
}

.carouselWrapper{
    margin: 0 auto;
    width: 270px;
    height: 270px;
}

.textWrapper{
    width: 100%;
    position: absolute;
    top: 360px;
    margin: 0 auto;
}

.mainTextTop{
    position: relative;
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 64px;
    color: #8C52FF ;
}

.mainTextMiddle{
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 44px;
    color: #8C52FF ;
}

.mainTextBottom{
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 40px;
    color: #8C52FF ;
}

.buttonWrapper{
    position: absolute;
    width: 100%;
    top: 600px;
    margin: 0 auto;
}

.ticketButton{
    width: 350px;
    margin:0 auto;
    display:block;
    height: 50px;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    border: none;
    background-color: #8C52FF ;
    color: rgb(230, 230, 230);
}

.more{
    width: 100%;
    position: absolute;
    top: 680px;
    margin: 0 auto;
}

.moreText{
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    color: #F6CB4B;
}