.wrapper {
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    background-color: rgb(34,34,34);
    width: 100%;
    height: auto;
    font-family: 'Open Sans', sans-serif;
    grid-template-areas:
      ". content . ";
  }

.content{
    grid-area: content;
}

.title{
    color: rgb(246,203,75);
    font-size: 56px;
    text-align: center;
}

.description{
    color: rgb(231, 231, 231);
    text-align: left;
    margin-bottom: 20px;
    font-size: 18px;
}