
.wrapper {
    width: 100vw;
    height: 800px;
    background-color: rgb(34,34,34);
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". ."
      ". ."
      ". ."
      ". ."
      ". .";
}

.aboutItem{
    width: 100%;
    height: 100%;
    overflow-y: hidden;
}

.aboutItemLabel{
    width: 100%;
    height: 100%;
    background-color: rgb(34,34,34);
}

.aboutLabel{
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 20px;
    padding-top: 60px;
    color: rgba(255, 255, 255, 0.911);
}

.galleryImg{
    max-width: 100%;
    height: auto;
    overflow-y: hidden;
}