
.wrapper{
    width: 100vw;
    height: 650px;
    background-color: #8C52FF;
  }


.titleLabel{
    font-family: 'Open Sans', sans-serif;   
    color: #F6CB4B;
    font-size: 30px;
    text-align:center;
    position: relative;
    top: 30px;
    padding-bottom: 60px;
}

.buyTicketButton{
    width: 80%;
    left: 10%;
    position: relative;
    color: white;
    border-radius: 0;
    margin-bottom: 30px;
}


.descriptionWrapper{
    display: grid;
    grid-template-columns: 1fr 10fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 0px;
    grid-template-areas:
      ". content .";
}

.description{
    grid-area: content;
    color: rgb(231, 231, 231);
    font-family: 'Open Sans', sans-serif;   
}