.wrapper{
    width: 100vw;
    height: auto;
    background-color: rgb(34, 34, 34);
}

.labelWrapper{
    padding-top: 40px;
    width: 100%;
    top: 360px;
    margin: 0 auto;
}

.label{
    padding: 0;
    margin: 0;
    font-family: 'Open Sans', sans-serif;
    text-align: center;
    font-size: 24px;
    color: rgb(246,203,75);
}



.sponsorWrapper{
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 200px 200px;
  gap: 20px 0px;
  grid-template-areas:
    ". Logo28 ."
    ". Dorama ."
    ". Fuzetea ."
    ". Ivi ."
    ". Mosigra ."
    ". RedBull ."
    ". Skyeng ."
    ". Vkusvill ."
    ". Vsemaykiru ."
    ". . .";
}

.logoItem28{
    grid-area: Logo28;
}

.logoItemDorama{
    grid-area: Dorama;
}

.logoItemFuzetea{
    grid-area: Fuzetea;
}

.logoItemIvi{
    grid-area: Ivi;
}

.logoItemMosigra{
    grid-area: Mosigra;
}

.logoItemRedBull{
    grid-area: RedBull;
}

.logoItemSkyeng{
    grid-area: Skyeng;
}

.logoItemVkusvill{
    grid-area: Vkusvill;
}

.logoItemVsemaykiru{
    grid-area: Vsemaykiru;
}

.logoImg{
    max-width:100%;
    max-height:100%;
}