.wrapper{
    padding-top: 30px;
    top: 400vh;
    height: auto;
    max-width: 100%;
    background-color: rgb(34,34,34);
    font-family: 'Open Sans', sans-serif;
}

.infoPhoto{
    width: 600px;
    height: 400px;
    display: inline;
}

.infoPhotoDiv{
    display: inline;
}

.mainText{
    display: inline;
    width: 300px;
}

.infoTitle{
    font-size: 25px;
    position: relative;
    left: 30px;
    top: 10px;
    color: #E3E3E3;
}


.infoAddress{
    font-size: 20px;
    position: relative;
    left: 30px;
    top: 10px;
    color: #E3E3E3;
}

.infoTip{
    position: absolute;
    vertical-align: middle;
    right: 0px;
    z-index: 999;
    width: 100%;
    height: 150px;
    background-color: rgb(34,34,34);

}

.location{
    z-index: 800;
    padding-top: -100px;

}

.locationIcon{
    text-align: left;
    position: relative;
    top: 20px;
    left: 30px;
}

.yandexRef{
    color:  #F6CB4B;
    font-size: 18px;
    text-decoration: none;
}